body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-y: scroll;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }
  
  .next-steps .fa-link {
    margin-right: 5px;
  }
  
  /* Fix for use only flexbox in content area */
  .next-steps .row {
    margin-bottom: 0;
  }
  
  .next-steps .col-md-5 {
    margin-bottom: 3rem;
  }
  
  @media (max-width: 768px) {
    .next-steps .col-md-5 {
      margin-bottom: 0;
    }
  }
  
  .spinner {
    position: absolute;
    display: flex;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    background-color: white;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  
  .result-block-container .result-block {
    opacity: 1;
  }
  
  .loading {
    display: flex;
    min-height: 500px;
    align-items: center;
    justify-content: center;
  }
  
  .userImg {
    border-radius: 100px;
    display: block;
    height: 100px;
    margin: 0 auto;
    width: 100px;
  }
  
  .hero .app-logo {
    max-width: 10.5rem;
  }

  .logo-coffe {
    background-image: url("logo_transparent.png");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 0px;
    margin: 0px;
    width: 150px;
    height: 50px;

  }

